export default {
  name: "o-modal-dialog",
  props: {
    id: {
      type: String,
      default: () => "modal-dialog"
    },
    title: {
      type: String,
      default: () => "Modal Dialog"
    },
    theme: {
      type: String,
      default: () => "light"
    }
  },
  computed: {
    compTheme() {
      if (this.theme === "light") {
        return ["bg-light", "text-dark"].join(" ");
      }
      return ["bg-dark", "text-light"].join(" ");
    }
  }
};